<template>
  <y-batch-import
    v-bind="$attrs"
    :columns="columns"
    v-on="$listeners"
  >
    <slot></slot>
  </y-batch-import>
</template>

<script>
import { priceUnitOptions } from './unit'
export default {
  name: 'YSkuImport',
  data() {
    return {
      columns: [
        {
          label: 'ID',
          prop: 'skuId',
          type: 'input',
          required: false,
          maxLength: '32'
        },
        {
          label: 'id',
          prop: 'externalSkuId',
          type: 'input',
          required: false,
          maxLength: '64'
        },
        {
          label: 'id',
          prop: 'ylzSkuId',
          type: 'input',
          required: false,
          maxLength: '64'
        },
        {
          label: '',
          prop: 'name',
          type: 'input',
          required: true,
          maxLength: '64'
        },
        {
          label: '',
          prop: 'brand',
          type: 'input',
          maxLength: '32'
        },
        {
          label: '',
          prop: 'spec',
          type: 'input',
          required: true,
          maxLength: '32'
        },
        {
          label: '',
          prop: 'oneCategory',
          type: 'input',
          required: true,
          maxLength: '20'
        },
        {
          label: '',
          prop: 'twoCategory',
          type: 'input',
          required: true,
          maxLength: '20'
        },
        {
          label: '',
          prop: 'threeCategory',
          type: 'input',
          required: true,
          maxLength: '20'
        },
        {
          label: '',
          prop: 'manufacturer',
          type: 'input',
          maxLength: '32'
        },
        {
          label: '',
          prop: 'shelfLifeDays',
          type: 'input',
          integer: true,
          required: true
        },
        {
          label: '',
          prop: 'priceUnit',
          type: 'select',
          options: priceUnitOptions,
          required: true
        },
        {
          label: '(,)',
          prop: 'normal',
          type: 'select',
          options: [{ label: '', value: '' },
            { label: '', value: '' }],
          required: true
        },
        {
          label: '(%)',
          prop: 'salesTaxRate',
          type: 'input',
          required: true,
          number: true,
          precision: 1,
          max: 99.9
        },
        {
          label: '(%)',
          prop: 'procurementTaxRate',
          type: 'input',
          required: true,
          number: true,
          precision: 1,
          max: 99.9
        },
        {
          label: '',
          prop: 'saleType',
          type: 'select',
          options: [
            // { label: '-', value: '-' },
            { label: '-', value: '-' },
            { label: '', value: '' },
            { label: '', value: '' }
          ],
          required: true
        },
        {
          label: '',
          prop: 'temperature',
          type: 'select',
          options: [{ label: '-18', value: '-18' },
            { label: '08', value: '08' },
            { label: '', value: '' }],
          required: true
        }
      ]
    }
  }
}
</script>

