<template>
  <div :key="key" class="y-card-form">
    <div v-if="withHeader" class="card-form-header-wrapper">
      <h3 class="title">{{ title }} <slot name="title"> </slot></h3>
      <slot name="header-right"></slot>
    </div>
    <div class="card-form-body">
      <y-form
        :key="key"
        ref="form"
        v-model="formData"
        :validate-prop="field"
        :config="config"
        :label-suffix="labelSuffix"
        label-position="right"
        :label-width="labelWidth"
        inline
      ></y-form>
    </div>
  </div>
</template>
<script>
const cid = Math.random().toString(32).replace('.', '')
export default {
  name: 'YCardForm',
  model: {
    prop: 'form',
    event: 'input'
  },
  props: {
    withHeader: {
      type: Boolean,
      default: true
    },
    title: {
      type: String,
      default: ''
    },
    span: {
      type: [String, Number],
      default: 3
    },
    offset: {
      type: [String, Number],
      default: 20
    },
    labelWidth: {
      type: String,
      default: '150px'
    },
    labelSuffix: {
      type: String,
      default: ''
    },
    field: {
      type: [String, Array],
      default: ''
    },
    form: {
      type: Object,
      default: () => {
        return {
          supplierName: '',
          supplierType: '',
          contactName: '',
          contactNumber: '',
          contactAddress: '',
          expireTime: '',
          deliveryDays: '',
          unifiedSocialCreditCode: '',
          operatingPeriod: ''
        }
      }
    },
    config: {
      type: Object,
      default: () => {
        return {
          supplierName: {
            label: '',
            prop: 'supplierName',
            fieldType: 'Input',
            rules: [{ required: true, message: '', trigger: ['blur'] }]
          },
          supplierType: {
            label: '',
            prop: 'supplierType',
            fieldType: 'Select',
            options: []
          },
          contactName: {
            label: '',
            prop: 'contactName',
            fieldType: 'Input'
          },
          contactNumber: {
            label: '',
            prop: 'contactNumber',
            fieldType: 'Input'
          },
          contactAddress: {
            label: '',
            prop: 'contactAddress',
            fieldType: 'Input'
          },
          expireTime: {
            label: '',
            prop: 'expireTime',
            fieldType: 'Input'
          },
          deliveryDays: {
            label: '',
            prop: 'deliveryDays',
            fieldType: 'Input'
          },
          unifiedSocialCreditCode: {
            label: '',
            prop: 'unifiedSocialCreditCode',
            fieldType: 'Input',
            number: false
          },
          operatingPeriod: {
            label: '',
            prop: 'operatingPeriod',
            fieldType: 'Radio',
            options: [{ label: '', value: 1 }, { label: '', value: 0 }]
          }
        }
      }
    }
  },
  data() {
    return {
      formData: this.form,
      key: cid,
      cols: this.span
    }
  },
  watch: {
    formData: {
      handler(val) {
        // formDataform
        this.$emit('input', val)
      },
      deep: true
    }
  },
  mounted() {
    this.init()
    window.addEventListener('resize', this.init)
  },
  destroyed() {
    window.removeEventListener('resize', this.init)
  },
  methods: {
    init() {
      const form = this.$refs.form.$el
      const formItems = form.querySelectorAll('.el-form-item')
      const formWidth = form.clientWidth
      this.setCols(formWidth)

      for (const item of formItems) {
        // el-form-itemwidth
        const itemWidth = Math.floor(formWidth / this.cols)
        // el-form-item__labelpaddingRight
        const paddingRight = parseInt(
          getComputedStyle(item.querySelector('.el-form-item__label'))
            .paddingRight
        )
        // el-form-item__labelwidth
        const labelWidth = item
          .querySelector('.el-form-item__label')
          .getBoundingClientRect().width
        // el-form-item__contentwidth
        const contentWidth = itemWidth - this.offset - labelWidth - paddingRight
        // el-form-item__contentwidth
        const contentDom = item.querySelector('.el-form-item__content')
        contentDom.style.width = `${contentWidth}px`
      }
    },
    setCols(formWidth) {
      switch (true) {
        case formWidth > 1240:
          // cols44
          if (this.cols >= 3) {
            this.cols = 4
          }
          break
        case formWidth > 992:
          if (this.cols >= 3) {
            this.cols = 3
          }
          break
        case formWidth > 768:
          if (this.cols >= 2) {
            this.cols = 2
          }
          break
        default:
          this.cols = 1
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.y-card-form {
  background-color: #fff;
  padding: 19px 24px 24px;
  box-sizing: border-box;
  border-radius: 4px;
  .title {
    display: inline-flex;
    align-items: center;
    margin: 0 0 27px 0;
    padding: 0;
    font-size: 18px;
    font-family: PingFang-SC-Heavy, PingFang-SC;
    font-weight: 800;
    color: #262626;
  }
}
</style>
