<template>
  <el-popover class="set-columns" placement="bottom" trigger="click">
    <i slot="reference" class="iconfont icon-bianji"> </i>
    <div class="columns-top-set">
      <el-checkbox v-model="checkAll" @change="handleCheckAllChange"></el-checkbox>
      <span>{{ selectedNum }}</span>
      <el-button type="text" @click="handleResetColumns"></el-button>
    </div>
    <div class="columns-list">
      <Draggable v-model="columnList" :scroll="true" :delay="50" handle=".mover" chosen-class="chosen-item" group="list" animation="250" @end="handleDragEnd">
        <div v-for="(col, index) in columnList" :key="index" class="list-item">
          <el-checkbox v-model="col.showCol" :disabled="disabledCols(col)" @change="handleCheckChange">
            <span class="mover">{{ col.label }}</span>
          </el-checkbox>
          <div class="left-right">
            <el-tooltip effect="dark" :open-delay="300" content="" placement="top">
              <i class="icon el-icon-back" :class="{'light': col.fixed === 'left'}" @click="handleFixed(col, 'left')"></i>
            </el-tooltip>
            <el-tooltip effect="dark" :open-delay="300" content="" placement="top">
              <i class="icon el-icon-right" :class="{'light': col.fixed === 'right'}" @click="handleFixed(col, 'right')"></i>
            </el-tooltip>
          </div>
        </div>
      </Draggable>
    </div>
  </el-popover>
</template>
<script>
import { cloneDeep } from 'lodash'
import Draggable from 'vuedraggable'
export default {
  name: 'SetColumns',
  components: { Draggable },
  props: {
    value: { //  
      type: Array,
      default: () => []
    },
    originColumns: { // 
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      checkAll: false,
      columnList: this.getColumnList() // 
    }
  },
  computed: {
    selectedNum() {
      return this.columnList.filter(im => im.showCol).length
    }
  },
  watch: {
    value(val) { //  
      this.columnList = this.getColumnList()
    }
  },
  created() {
    this.checkAll = this.columnList.every(im => im.showCol)
  },
  methods: {
    // 
    getColumnList(isOrigin = false) {
      // isOrigin  
      const columns = isOrigin ? this.originColumns : this.value
      const columnList = cloneDeep(columns).map((im, i) => {
        im.fixed = im.fixed || null
        im.showCol = Object.prototype.hasOwnProperty.call(im, 'showCol') ? im.showCol : true
        return im
      })
      return columnList
    },
    //  
    updateColumns() {
      const columnList = this.columnList
      this.$emit('input', [])
      this.$nextTick(() => this.$emit('input', columnList))
    },
    // 
    handleCheckAllChange(val) {
      this.columnList.forEach(col => {
        col.showCol = val || this.disabledCols(col)
      })
      this.updateColumns()
    },
    // 
    handleCheckChange(val) {
      this.checkAll = val ? this.columnList.every(im => im.showCol) : val
      this.updateColumns()
    },
    // 
    handleResetColumns() {
      this.columnList = this.getColumnList(true)
      this.checkAll = this.columnList.every(im => im.showCol)
      this.updateColumns()
    },
    // 
    handleDragEnd() {
      this.updateColumns()
    },
    //  
    disabledCols(col) {
      return Boolean(col.fixed) || col.prop === 'action' || col.label === ''
    },
    // 
    handleFixed(col, type) {
      col.fixed = (col.fixed === type) ? null : type //    
      this.updateColumns()
    }
  }
}
</script>
<style lang="scss" scoped>
.iconfont {
  margin: 0 0 0 10px;
  font-size: 16px;
  cursor: pointer;
  color: #8c8c8c;
}
.columns-top-set {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 10px;
  border-bottom: 1px solid #ededed;
  margin-bottom: 5px;
}
.columns-list {
  display: flex;
  flex-direction: column;
  padding-right: 20px;
  box-sizing: border-box;
  min-width: 200px;
  .chosen-item {
    border-top: 2px solid #409eff;
  }
  .list-item {
    padding: 4px 0;
    display: flex;
    justify-content: space-between;
    .left-right {
      display: none;
    }
  }
  .list-item:hover {
    .left-right {
      display: block;
      .light {
        color: #409eff;
      }
      i {
        margin-left: 15px;
        cursor: pointer;
      }
    }
  }
}
</style>
