<template>
  <div class="card">
    <h2 v-if="hasTitle" class="card-title" :style="{fontSize: `${size}px`}">{{ title }}<slot name="title"> </slot></h2>
    <el-row class="card-row">
      <el-col v-for="(item, index) in columns" :key="index" class="card-col" :span="span">
        <span class="card-label">{{ item.label }}:</span>
        <template v-if="item.render">
          <div class="card-value">
            <expandDom
              :render="item.render"
              :data="data"
              :prop="item.prop"
            >
            </expandDom>
          </div>
        </template>
        <y-text v-else :content="item.formatter ? item.formatter(data[item.prop]) : data[item.prop]" class="card-value"></y-text>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'YCard',
  components: {
    expandDom: {
      functional: true,
      props: {
        render: Function,
        data: [Array, Object],
        prop: String
      },
      render(h, ctx) {
        const params = {}
        if (ctx.props.data) {
          params.data = ctx.props.data
        }
        if (ctx.props.prop) {
          params.prop = ctx.props.prop
        }
        return ctx.props.render && ctx.props.render(h, params)
      }
    }
  },
  props: {
    title: {
      type: String,
      default: 'Spu '
    },
    data: {
      type: Object,
      default: () => {
        return {
          spuName: 'AAA',
          shelfLife: 15,
          type: 'standard',
          brand: '',
          backType: '///',
          saleRate: '0.00',
          salePrice: '',
          address: '',
          storageTemperatureZone: '',
          purchaseTaxRate: '0.10',
          minSpec: '500ml/',
          averageWeight: '200g',
          shape: ''
        }
      }
    },
    columns: {
      type: Array,
      default: () => [
        {
          label: 'SPU ',
          prop: 'spuName'
        },
        {
          label: '',
          prop: 'shelfLife'
        },
        {
          label: '',
          prop: 'type',
          formatter(val) {
            if (val === 'standard') {
              return ''
            }
          }
        },
        {
          label: '',
          prop: 'brand',
          render(h, { data, prop }) {
            return <y-image src='11' width='60'></y-image>
          }
        },
        {
          label: '',
          prop: 'backType'
        },
        {
          label: '',
          prop: 'saleRate',
          formatter(val) {
            return `${val}%`
          }
        },
        {
          label: '',
          prop: 'salePrice'
        },
        {
          label: '',
          prop: 'address'
        },
        {
          label: '',
          prop: 'storageTemperatureZone'
        },
        {
          label: '',
          prop: 'purchaseTaxRate',
          formatter(val) {
            return `${val}%`
          }
        },
        {
          label: '',
          prop: 'minSpec'
        },
        {
          label: '',
          prop: 'averageWeight'
        },
        {
          label: '',
          prop: 'shape'
        }

      ]
    },
    cols: {
      type: [String, Number],
      default: 4
    },
    size: {
      type: [String, Number],
      default: 18
    }
  },
  data() {
    return {
    }
  },
  computed: {
    span() {
      if (!this.cols) return 24
      return Math.round(24 / this.cols)
    },
    hasTitle() {
      if (this.title === '' || this.title === undefined || this.title === null) {
        return false
      } else {
        return true
      }
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {

    }
  }
}
</script>

<style lang="scss" scoped>
.card {
  padding: 18px 24px 4px;
  background: #FFFFFF;
  border-radius: 4px;
  &-title{
    margin: 0;
    font-size: 18px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: bold;
    color: rgba(0, 0, 0, 0.85);
    margin-bottom: 20px;
  }
  &-col {
    margin-bottom: 16px;
  }
  &-label {
    display: table-cell;
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #262626;
    line-height: 22px;
    white-space: nowrap;
    vertical-align: top;
  }
  &-value {
    display: inline-flex;
    display: table-cell;
    padding-left: 8px;
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #575757;
    line-height: 22px;
  }
}
</style>
