<template>
  <div v-if="!refresh">
    <y-upload-list
      :ref="refUpload"
      :disabled="disabled"
      :class="{'off-add': limit<=defaultFile.length, 'y-upload': true}"
      :list-type="listType"
      action=""
      :on-preview="handlePictureCardPreview"
      :before-upload="handleBeforeUpload"
      :on-remove="handleRemove"
      :file-list="defaultFile"
      :is-dragging="isDragging"
      @dragend="dragend"
    >
      <i class="el-icon-plus"></i>
      <template slot="file" slot-scope="{file}">
        <slot name="file" :file="file"></slot>
      </template>
    </y-upload-list>
    <!-- <y-upload-list
      class="y-upload-list"
      list-type="picture-card"
      :files="fileList"
    ></y-upload-list> -->
    <!-- <el-dialog :append-to-body="true" :visible.sync="dialogVisible">
      <img style="width:100%" :src="dialogImageUrl" alt="">
    </el-dialog> -->
    <!--  -->
    <y-image-viewer :visible.sync="dialogVisible" :url-list="[dialogImageUrl]"></y-image-viewer>

    <el-dialog :append-to-body="true" :visible.sync="modalImg" title="">
      <div class="cropper">
        <vueCropper
          ref="cropper"
          style="width:100%;height:500px;"
          :high="option.high"
          :img="option.img"
          :output-size="option.size"
          :output-type="option.outputType"
          :info="true"
          :full="option.full"
          :can-move="option.canMove"
          :can-move-box="option.canMoveBox"
          :original="option.original"
          :auto-crop="option.autoCrop"
          :fixed="option.fixed"
          :fixed-number="option.fixedNumber"
          :center-box="option.centerBox"
          :info-true="option.infoTrue"
          :fixed-box="option.fixedBox"
          :can-scale="option.canScale"
        >
        </vueCropper>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="modalImg = false"> </el-button>
        <el-button :loading="loading" type="primary" @click="uploadSuccess()"> </el-button>
      </span>
      <!--cropper-->
    </el-dialog>
  </div>
</template>
<script>

import { VueCropper } from 'vue-cropper'

const defaultAPI = function(params) {
  return new Promise(resolve => {
    resolve(
      {
        data: {
          url: 'https://yyx-temp.oss-cn-chengdu.aliyuncs.com/common/image/png/abe074b524fd8888f85b58e4ce09832e.png'
        }
      }
    )
  })
}
export default {
  name: 'YUpload',
  components: {
    VueCropper
  },
  props: {
    api: {
      type: Function,
      default: defaultAPI
    },
    disabled: {
      type: Boolean, // disabled === true  
      default: false
    },
    limit: {
      type: Number,
      default: 4
    },
    refUpload: {
      type: String,
      default: 'upload'
    },
    fileList: {
      type: Array,
      default: () => []
    },
    fixedBox: {
      type: Boolean,
      default: () => true
    }, //  
    fixed: {
      type: Boolean,
      default: () => true
    }, // 
    fixedNumber: {
      type: Array,
      default: () => [1, 1]
    }, // 
    /*  */
    isCropper: {
      type: Boolean,
      default: () => true
    },
    isCompress: {
      type: Boolean,
      default: false
    },
    // fixedNumber
    compressRatio: {
      type: Number,
      default: 360
    },
    maxSize: {
      type: Number,
      default: 1
    },
    allowPdf: {
      type: Boolean,
      default: false
    },
    isDragging: {
      type: Boolean,
      default: false
    },
    listType: {
      type: String,
      default: 'picture-card'
    }
  },
  data() {
    return {
      refresh: false,
      loading: false,
      dialogImageUrl: '',
      defaultFile: [],
      img: '',
      dialogVisible: false,
      modalImg: false
    }
  },
  computed: {
    option() {
      return {
        img: this.img, // 
        info: true, // 
        high: true, // dpr
        outputSize: 0.8, // 
        outputType: 'png', // 
        canScale: true, // 
        autoCrop: true, // 
        fixedBox: this.fixedBox, //  
        fixed: this.fixed, // 
        fixedNumber: this.fixedNumber, // 
        full: true, // 
        canMoveBox: false, // 
        original: false, // 
        centerBox: true, // 
        infoTrue: false // true  false 
      }
    }
  },
  watch: {
    fileList() {
      this.formatDefaultFile()
    }
  },
  mounted() {
    this.formatDefaultFile()
  },
  methods: {
    dragend(e) {
      this.$emit('dragend', e.map(i => i.url))
    },
    handleRemove(file, fileList, index) {
      this.default = fileList
      this.defaultFile = fileList
      this.$emit('on-remove', {
        file,
        fileList,
        index
      })
    },
    handlePictureCardPreview(file) {
      if (file.pdf) {
        window.open(file.pdf)
        return
      }
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    },
    async uploadSuccess(isPdf) {
      this.loading = true
      if (this.isCropper && !isPdf) {
        this.$refs.cropper.getCropBlob(async(data) => {
          let base64 = await this.blobToDataURL(data) // base64
          if (this.isCompress) {
            // 
            base64 = await this.compress(base64) // 
          }
          const blob = this.dataURLtoBlob(base64) // blob
          try {
            const formData = new FormData()
            formData.append('file', this.blobtoFile(blob))
            const res = await this.api(formData)
            this.$refs[this.refUpload].fileList.push({
              url: res.data.url,
              name: '',
              status: 'finished'
            })
            this.defaultFile = this.$refs[this.refUpload].fileList
            this.handleSuccess(res)
            this.modalImg = false
          } finally {
            this.loading = false
          }
        })
      } else {
        const formData = new FormData()
        formData.append('file', this.fileinfo)
        const res = await this.api(formData)
        this.$refs[this.refUpload].fileList.push({
          url: res.data.url,
          name: '',
          status: 'finished'
        })
        this.defaultFile = this.$refs[this.refUpload].fileList
        this.handleSuccess(res)
        this.modalImg = false
        this.loading = false
      }
    },
    // blobFile 
    blobtoFile(blob) {
      const name = this.fileinfo.name.split('.')[0] + '.png'
      return new window.File([blob], name, { type: 'image/png' })
    },
    // Base64blob 
    dataURLtoBlob(dataurl) {
      var arr = dataurl.split(',')
      var mime = arr[0].match(/:(.*?);/)[1]
      var bstr = atob(arr[1])
      var n = bstr.length
      var u8arr = new Uint8Array(n)
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n)
      }
      return new Blob([u8arr], { type: mime })
    },
    // blobBase64 
    blobToDataURL(blob) {
      return new Promise((resolve) => {
        const a = new FileReader()
        a.readAsDataURL(blob)
        a.onload = (res) => {
          resolve(res.currentTarget.result)
        }
      })
    },
    // 
    compress(src) {
      return new Promise((resolve) => {
        const img = new Image()
        img.src = src
        img.onload = () => {
          const canvas = document.createElement('canvas')
          const ctx = canvas.getContext('2d')
          //  compressRatio 360
          const width = this.compressRatio
          const height = this.compressRatio * (img.height / img.width)
          canvas.width = width
          canvas.height = height
          // 
          ctx.fillStyle = '#fff'
          ctx.fillRect(0, 0, canvas.width, canvas.height)
          ctx.drawImage(img, 0, 0, width, height)
          // 
          const ndata = canvas.toDataURL('image/jpeg', 1)
          resolve(ndata)
        }
      })
    },
    handleBeforeUpload(file) {
      const check = this.fileList.length >= this.limit
      if (check) {
        this.$message({
          message: '',
          type: 'warning'
        })
        return false
      }
      const fileSize = file.size / 1024 / 1024
      if (fileSize > this.maxSize) {
        this.$message({
          message: ` ${this.maxSize}MB!`,
          type: 'warning'
        })
        return false
      }
      const testmsg = file.name.substring(file.name.lastIndexOf('.') + 1)
      let imgExt = /(jpg|JPG|bmp|BMP|gif|GIF|ico|ICO|pcx|PCX|jpeg|JPEG|tif|TIF|png|PNG|raw|RAW|tga|TGA)$/
      if (this.allowPdf) {
        imgExt = /(jpg|JPG|bmp|BMP|gif|GIF|ico|ICO|pcx|PCX|jpeg|JPEG|tif|TIF|png|PNG|raw|RAW|tga|TGA|pdf)$/
      }
      if (!imgExt.test(testmsg)) {
        this.$message({
          message: `${this.allowPdf ? 'pdf' : ''}`,
          type: 'warning'
        })
        return false
      }
      this.fileinfo = file
      const data = window.URL.createObjectURL(new Blob([file]))
      this.img = data
      if (this.isCropper && testmsg !== 'pdf') {
        this.modalImg = true
      } else {
        this.uploadSuccess(testmsg === 'pdf')
      }
      return false // 
    },
    formatDefaultFile() {
      this.defaultFile = this.fileList.map(item => {
        if (typeof item === 'string') {
          if (item.split('.').pop() === 'pdf') {
            return {
              pdf: item,
              url: 'https://yyx-mall.oss-cn-chengdu.aliyuncs.com/common/image/png/icon-pdf.png',
              name: '',
              status: 'finished'
            }
          } else {
            return {
              url: item,
              name: '',
              status: 'finished'
            }
          }
        } else {
          const url = item.url
          if (url.split('.').pop() === 'pdf') {
            return {
              pdf: url,
              url: 'https://yyx-mall.oss-cn-chengdu.aliyuncs.com/common/image/png/icon-pdf.png',
              name: '',
              status: 'finished'
            }
          } else {
            return {
              url: url,
              name: '',
              status: 'finished'
            }
          }
        }
      })
      this.refresh = true
      this.$nextTick(() => {
        this.refresh = false
      })
    },
    handleSuccess(response) {
      this.$emit('on-success', {
        file: response.data,
        fileList: this.defaultFile
      })
    }
  }
}
</script>

<style>
.crop-info {
  display: none;
}
.off-add .el-upload--picture-card {
  display: none;
}
</style>
