<template>
  <div :class="{'batch-import': true, 'isStreamline': isStreamline}" v-bind="$attrs" v-on="$listeners">
    <template v-if="isStreamline">
      <el-upload
        ref="upload"
        action=""
        :multiple="false"
        :show-file-list="false"
        accept="csv, application/vnd.ms-excel, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
        :http-request="httpRequest"
      >
        <!--  -->
        <div slot="tip" class="tips">
          <slot></slot>
        </div>
        <div slot="tip" class="upload">
          <el-button
            slot="trigger"
            type="primary"
            icon="el-icon-upload"
            @click="handleClick"
          >{{ btnText }}</el-button>
          <div
            slot="tip"
            class="el-upload__tip mt-10"
          >
            excel<span v-if="size">{{ size }}kb</span>
            <el-link
              v-if="isExport"
              class="template"
              type="primary"
              @click="$emit('download')"
            >
              {{ downloadText }}
            </el-link>
            <el-link
              v-else
              class="template"
              type="primary"
              :href="downloadUrl"
            >{{ downloadText }}</el-link>
          </div>
        </div>
      </el-upload>
    </template>
    <template v-else>
      <!--  -->
      <el-button
        :type="$attrs.type"
        icon="el-icon-upload"
        @click="dialogVisible=true"
      >{{ btnText }}</el-button>
      <!--  end -->
      <!--   -->
      <el-dialog
        :title="btnText"
        :visible.sync="dialogVisible"
        :close-on-click-modal="false"
        modal-append-to-body
        append-to-body
        lock-scroll
        destroy-on-close
        width="92%"
        :before-close="handleBeforeClose"
        @opened="handleOpen"
      >
        <el-upload
          ref="upload"
          action=""
          :multiple="false"
          :show-file-list="false"
          accept="csv, application/vnd.ms-excel, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
          :http-request="httpRequest"
        >
          <!--  -->
          <div slot="tip" class="tips">
            <slot></slot>
          </div>
          <div slot="tip" class="upload">
            <el-button
              slot="trigger"
              type="primary"
              icon="el-icon-upload"
              @click="handleClick"
            ></el-button>
            <div
              slot="tip"
              class="el-upload__tip mt-10"
            >
              excel<span v-if="size">{{ size }}kb</span>
              <el-link
                v-if="isExport"
                class="template"
                type="primary"
                @click="$emit('download')"
              >
                {{ downloadText }}
              </el-link>
              <el-link
                v-else
                class="template"
                type="primary"
                :href="downloadUrl"
              >{{ downloadText }}</el-link>
            </div>
          </div>
        </el-upload>
        <el-button-group>
          <el-button
            v-if="hasEditButton && tableData.length"
            class="mt-10"
            @click="handleToggleEdit"
          >{{ !isEdit ?'' : '' }}</el-button>
        </el-button-group>
        <!--  -->
        <el-table
          v-if="multiHeader"
          ref="table"
          :data="tableData"
          style="width: 100%"
        >
          <template v-for="col in columns">
            <!-- children -->
            <el-table-column
              v-if="!col.children"
              :key="col.prop"
              :prop="col.prop"
              :label="col.label"
              v-bind="col"
            >
            </el-table-column>
            <!-- children -->
            <el-table-column
              v-else
              :key="col.prop"
              :prop="col.prop"
              :label="col.label"
              v-bind="col"
            >
              <el-table-column
                v-for="childCol in col.children"
                :key="childCol.prop"
                :prop="childCol.prop"
                :label="childCol.label"
                v-bind="childCol"
              >
              </el-table-column>
            </el-table-column>
          </template>

        </el-table>
        <!--  -->
        <y-table
          v-else
          ref="table"
          class="mt-10"
          :max-height="312"
          :data="tableData"
          :columns="currentColumns"
          :pagination="importPagination"
          :total="total"
          :reload="reloadData"
          :col-index="1"
        ></y-table>
        <span
          slot="footer"
          class="dialog-footer"
        >
          <el-pagination
            v-if="multiHeader"
            :page-size.sync="multipage.size"
            :pager-count="7"
            :current-page.sync="multipage.current"
            layout="total, sizes, prev, pager, next, jumper"
            :page-sizes="[10, 20]"
            hide-on-single-page
            :total="total"
            @size-change="sizeChange"
            @current-change="currentChange"
          >
          </el-pagination>
          <el-button @click="handleCancel"> </el-button>
          <el-button
            type="primary"
            :disabled="!total"
            @click="handleConfirm"
          > </el-button>
        </span>
      </el-dialog>
      <!--  end -->
    </template>
  </div>
</template>

<script>
import XLSX from 'xlsx'
import { merge, find, isEmpty, cloneDeep, debounce, round } from 'lodash'
import { Message } from 'element-ui'
import moment from 'moment'
// import Vue from 'vue'
export default {
  name: 'YBatchImport',
  props: {
    isStreamline: {
      type: Boolean,
      default: false
    },
    hasEditButton: {
      type: Boolean,
      default: true
    },
    btnText: {
      type: String,
      default: ''
    },
    downloadText: {
      type: String,
      default: ''
    },
    isExport: {
      type: Boolean,
      default: false
    },
    size: {
      type: [String, Number],
      default: 0
    },
    downloadUrl: {
      type: String,
      required: true
    },
    // 
    uploadSuccess: {
      type: Boolean,
      default: false
    },
    columns: {
      type: Array,
      default() {
        return [
          {
            label: '',
            prop: 'name',
            type: 'input'
          },
          {
            label: '',
            prop: 'brand',
            type: 'input'
          },
          {
            label: '',
            prop: 'spec',
            type: 'input'
          },
          {
            label: '',
            prop: 'oneCategory',
            type: 'input'
          },
          {
            label: '',
            prop: 'twoCategory',
            type: 'input'
          },
          {
            label: '',
            prop: 'threeCategory',
            type: 'input'
          },
          {
            label: '',
            prop: 'manufacturer',
            type: 'input'
          },
          {
            label: '',
            prop: 'shelfLifeDays',
            type: 'input'
          },
          {
            label: '',
            prop: 'priceUnit',
            type: 'input'
          },
          {
            label: ',',
            prop: 'normal',
            type: 'input'
          },
          {
            label: '(%)',
            prop: 'salesTaxRate',
            type: 'input'
          },
          {
            label: '',
            prop: 'saleType',
            type: 'input'
          },
          {
            label: '(%)',
            prop: 'procurementTaxRate',
            type: 'input'
          },
          {
            label: '',
            prop: 'temperature',
            type: 'select',
            options: [{ label: '-18C', value: 'FREEZE' },
              { label: '0C8C', value: 'COLD' },
              { label: '', value: 'NORMAL' }]
          }
        ]
      }
    },
    multiHeader: {
      type: Boolean,
      default: false
    },
    range: {
      type: Number,
      default: 1
    }
  },
  data() {
    return {
      multipage: {
        size: 10,
        current: 1
      },
      dialogVisible: false,
      tableData: [],
      total: 0,
      queryParams: {
        current: 1,
        size: 10
      },
      sheetHeader: {},
      dbData: [],
      currentColumns: [],
      isEdit: false,
      importPagination: {
        small: false,
        background: false,
        'page-size': 10,
        total: 0,
        'page-count': 0,
        'pager-count': 7,
        'current-page': 1,
        layout: 'total, sizes, prev, pager, next, jumper',
        'page-sizes': [10, 20],
        'popper-class': '',
        'prev-text': '',
        'next-text': '',
        disabled: false,
        'hide-on-single-page': true
      },
      zIndex: -1

    }
  },
  watch: {
    uploadSuccess: {
      handler(val) {
        if (val) {
          this.dialogVisible = false
          this.tableData = []
          this.dbData = []
          this.total = 0
          this.isEdit = false
          this.$forceUpdate()
        }
      },
      deep: true,
      immediate: true
    }
  },
  created() {
    this.currentColumns = this.columns.map((item) => {
      return {
        label: item.label,
        prop: item.prop,
        render: (h, { row, index }) => {
          if (this.isEdit) {
            if (item.type === 'input') {
              return <y-input v-model_trim={this.tableData[index][item.prop]} size='small' maxLength={item.maxLength} clearable rules={row.rules} number={!!item.number} integer={!!item.integer} integerDigit={item.integerDigit} precision={item.precision} {...{ props: item }}></y-input>
            } else if (item.type === 'select') {
              return <el-select v-model={this.tableData[index][item.prop]} size='small' ref='select' clearable rules={row.rules} >
                {item.options.map((option) => {
                  return <el-option key={option.value}
                    label={option.label}
                    value={option.value}>
                  </el-option>
                })}
              </el-select>
            } else if (item.type === 'date-picker') {
              this.tableData[index][item.prop] = (moment(this.tableData[index][item.prop]).format('YYYY-MM-DD') === 'Invalid date' ? '' : moment(this.tableData[index][item.prop]).format('YYYY-MM-DD'))
              return <el-date-picker
                style={{ width: '95%', display: 'block' }}
                v-model={this.tableData[index][item.prop]}
                type='date'
                size='small'
                onChange={() => { this.tableData[index][item.prop] = (moment(this.tableData[index][item.prop]).format('YYYY-MM-DD') === 'Invalid date' ? '' : moment(this.tableData[index][item.prop]).format('YYYY-MM-DD')) }}
                placeholder=''>
              </el-date-picker>
            } else if (item.type === 'date-picker-time') {
              this.tableData[index][item.prop] = moment(this.tableData[index][item.prop]).format('YYYY-MM-DD HH:mm:ss') === 'Invalid date' ? '' : moment(this.tableData[index][item.prop]).format('YYYY-MM-DD HH:mm:ss')
              return <el-date-picker
                style={{ width: '95%', display: 'block' }}
                v-model={this.tableData[index][item.prop]}
                type='datetime'
                size='small'
                onChange={() => { this.tableData[index][item.prop] = moment(this.tableData[index][item.prop]).format('YYYY-MM-DD HH:mm:ss') === 'Invalid date' ? '' : moment(this.tableData[index][item.prop]).format('YYYY-MM-DD HH:mm:ss') }}
                placeholder=''>
              </el-date-picker>
            } else if (item.type === 'input-number') {
              return <YInputNumber v-model_trim={this.tableData[index][item.prop]} min={item.min} max={item.max} size='small' clearable rules={row.rules} {...{ props: item }}></YInputNumber>
            } else {
              return <span>{this.tableData[index][item.prop]}</span>
            }
          } else {
            return <div onClick={this.handleToggleEdit}>{row[item.prop]}</div>
          }
        }
      }
    })
  },
  mounted() {
  },
  methods: {
    handleOpen() {
      const tableDom = this.$refs?.table.$el
      if (!tableDom) return
      const tableBodyWrapper = tableDom.querySelector('.el-table__body-wrapper')
      // 
      tableBodyWrapper.addEventListener('scroll', debounce(this.handleTableScroll))
    },
    handleTableScroll(e) {
      const elPoppers = [...document.querySelectorAll('.el-select-dropdown.el-popper')]
      elPoppers.forEach(popper => {
        const zIndex = popper.style.zIndex
        if (zIndex > 0) {
          // zIndex
          this.zIndex = zIndex
        }
        this.$nextTick(() => {
          if (popper) {
            if (e.target.scrollTop > 5) {
              popper.style.zIndex = -1
            } else {
              popper.style.zIndex = this.zIndex
            }
          }
        })
      })
    },
    // 
    validate(data) {
      // indextableData
      this.loadData()
      return new Promise((resolve, reject) => {
        data.forEach((item, index) => {
          Object.keys(item).forEach(key => {
            // keycolumn
            const column = find(this.columns, (col) => {
              return col.prop === key
            })
            if (!isEmpty(column)) {
              // column,relationProp:
              if (column.required && (!column.relationProp || (column.relationProp && item[column.relationProp]) === '')) {
                // required
                if (item[key] === '' || item[key] === undefined || item[key] === null) {
                  // 
                  reject(`${index + 1}[${column.label}] `)
                }
              }
              if (column.pattern && item[key]) {
                // 
                if (!new RegExp(column.pattern).test(item[key])) {
                  if (column.message) {
                    reject(`${index + 1}[${column.label}]${column.message}`)
                  } else {
                    reject(`${index + 1}[${column.label}] `)
                  }
                }
              }
              // 
              if (column.max) {
                if (item[key] > column.max) {
                  // 
                  reject(`${index + 1}[${column.label}] ${column.max}`)
                }
              }
              // 
              if (column.max) {
                if (item[key] < column.min) {
                  // 
                  reject(`${index + 1}[${column.label}] ${column.min}`)
                }
              }
              // 
              if (column.maxLength) {
                if (item[key].length > column.maxLength) {
                  // 
                  reject(`${index + 1}[${column.label}] ${column.maxLength}`)
                }
              }
            }
          })
        })
        resolve(true)
      })
    },
    getValue(originvValue, options) {
      let value = ''
      options.find(option => {
        if (option.label === originvValue || option.value === originvValue) {
          value = option.value
          return true
        }
      })
      return value
    },
    // 
    handleBeforeClose(done) {
      this.handleCancel()
      done()
    },
    // 
    handleCancel() {
      this.tableData = []
      this.total = 0
      document.getElementsByName('file')[0].value = ''
      this.dialogVisible = false
    },
    // 
    handleConfirm() {
      this.mergeTable()
      if (this.multiHeader) {
        // 
        this.$emit('upload', this.dbData)
        return
      }
      this.validate(this.dbData).then(valid => {
        if (valid) {
          const uploadData = cloneDeep(this.dbData).map(item => {
            const obj = {}
            Object.keys(item).forEach(key => {
              // keycolumn
              const column = find(this.columns, col => {
                return col.prop.trim().includes(key)
              })
              if (column) {
                if (column.type === 'select') {
                  obj[column.prop] = this.getValue(item[key], column.options)
                } else {
                  obj[column.prop] = item[key]
                }
              }
            })
            return obj
          })
          this.$emit('upload', uploadData)
        }
      }).catch(err => {
        this.$message.warning(err)
      })
    },
    // 
    handleToggleEdit() {
      this.isEdit = !this.isEdit
      // tableDatadbData
      if (!this.isEdit) {
        this.mergeTable()
      }
    },
    // tableDatadbData
    mergeTable() {
      const { size, current } = this.queryParams
      const mergeData = this.tableData.map(item => {
        delete item.index
        return item
      })
      this.dbData.splice((current - 1) * size, size, ...mergeData)
    },
    // 
    loadData() {
      const { size, current } = this.queryParams
      this.tableData = this.dbData.slice((current - 1) * size, current * size).map((item, index) => {
        item.index = index
        return item
      })
    },
    sizeChange(size) {
      this.reloadData({ type: 'size-change', pageSize: size })
      this.multipage.current = 1
    },
    currentChange(current) {
      this.reloadData({ type: 'current-change', currentPage: current })
    },
    // 
    reloadData({ type, pageSize: size, currentPage }) {
      // 
      this.mergeTable()
      if (type === 'size-change') {
        // current1
        this.queryParams = merge(this.queryParams, { size, current: 1 })
      } else {
        // 
        this.queryParams = merge(this.queryParams, { current: currentPage })
      }
      this.loadData()
    },
    // dbData excel
    formatDbData(dbData) {
      if (!Array.isArray(dbData)) {
        throw new TypeError('Array')
      }
      const res = dbData.map(item => {
        const obj = {}
        Object.keys(item).forEach(key => {
          // keycolumn
          const column = find(this.columns, col => {
            return col.label.trim().includes(key)
          })
          if (typeof item[key] === 'number') {
            // 8
            item[key] = round(item[key], 8)
          }
          if (column) {
            if (column.type === 'date-picker') {
              const format = column.format || 'YYYY-MM-DD'
              if (!isNaN(item[key])) {
                obj[column.prop] = this.formateDate(Number(item[key]), format)
              } else {
                obj[column.prop] = (moment(item[key]).format(format) === 'Invalid date' ? '' : moment(item[key]).format(format))
              }
            } else {
              const format = column.format
              if (format && format.includes('YYYY-MM-DD') && !isNaN(item[key])) {
                obj[column.prop] = this.formateDate(Number(item[key]), format)
              } else {
                obj[column.prop] = item[key]
              }
            }
          } else {
            throw new TypeError('')
          }
        })
        return obj
      })
      return res
    },
    formateDate(value, format = 'YYYY-MM-DD') {
      // 11970-1-11900-1-125567 0 5 43

      // 218
      const timestamp = (value - 25567) * 24 * 3600000 - 5 * 60 * 1000 - 43 * 1000 - 24 * 3600000 - 8 * 3600000

      return moment(timestamp).format(format)
    },
    initData() {
      this.tableData = []
      this.total = 0
      this.queryParams = {
        current: 1,
        size: 10
      }
    },
    formatMultiDbData(exl) {
      const cloneExl = cloneDeep(exl)
      // 
      const sheetHeader = cloneExl.shift()
      for (const [key, value] of Object.entries(sheetHeader)) {
        if (value) {
          // value
          sheetHeader[value] = sheetHeader[key]
        }
      }
      this.sheetHeader = sheetHeader

      // 
      cloneExl.forEach(item => {
        for (const [key, value] of Object.entries(item)) {
          const headerKey = this.sheetHeader[key]
          if (headerKey) {
            item[headerKey] = value
            delete item[key]
          } else {
            item[key] = value
          }
        }
      })

      // labelprop
      cloneExl.forEach(item => {
        for (const [label, value] of Object.entries(item)) {
          const getCurrentColumn = (columns, label) => {
            let obj = null
            columns.some(col => {
              if (col.label === label && col.prop) {
                obj = col
                return obj
              }
              if (col.children) {
                obj = getCurrentColumn(col.children, label)
                return obj
              }
            })
            return obj
          }
          const currentColumn = getCurrentColumn(this.columns, label)

          if (currentColumn) {
            const key = currentColumn.prop
            item[key] = value
            delete item[label]
          }
        }
      })

      return cloneExl
    },
    // 
    httpRequest(e) {
      this.initData()
      const file = e.file // 
      if (!file) {
        // 
        return false
      } else if (!/\.(xls|xlsx)$/.test(file.name.toLowerCase())) {
        // 
        this.$message.error('xlsxlsx')
        return false
      }

      const fileReader = new FileReader()
      fileReader.onload = (ev) => {
        try {
          const data = ev.target.result
          const workbook = XLSX.read(data, {
            type: 'binary' // 
          })
          const exlname = this.sheetName || workbook.SheetNames[0] // 
          const exl = XLSX.utils.sheet_to_json(workbook.Sheets[exlname], { defval: '', range: this.range }) // json
          if (!exl) {
            this.$message.error('')
            return false
          }
          if (this.multiHeader) {
            // 
            this.dbData = this.formatMultiDbData(exl)
          } else {
            //  JSON  data 
            this.dbData = this.formatDbData(exl)
          }

          this.$emit('loadData', this.dbData)
          if (this.isStreamline) {
            this.$emit('upload', cloneDeep(this.dbData))
          } else {
            this.loadData()
            this.total = this.dbData.length
          }
          // document.getElementsByName('file')[0].value = '' // value
        } catch (e) {
          Message({
            showClose: true,
            message: e.message,
            type: 'error'
          })
          console.log('', e.message)
          this.initData()
          return false
        }
      }
      fileReader.readAsBinaryString(file)
    },
    handleClick(e) {
      e.stopPropagation()
      // 
      this.$refs.upload.$refs['upload-inner'].$refs.input.click()
    }
  }
}
</script>
<style lang="scss" scoped>
.batch-import {
  display: inline-block;
  margin-left: 10px;
}

::v-deep .el-upload {
  display: inline-block;
  width: 100%;
  text-align: left;
}
.upload {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}
.isStreamline {
  width: 100%;
}
.mt-10 {
  margin-top: 10px;
}
.template {
  position: relative;
  margin-left: 10px;
  top: -1px;
}
.dialog-footer{
  display: flex;
  justify-content: flex-end;
}
</style>

