<template>
  <div ref="buttonMore" :key="key" class="button-more">
    <el-dropdown
      v-if="hasShowDropdown"
      :size="size"
      trigger="click"
      class="color--primary"
      @command="handleCommand"
    >
      <el-button type="primary">
        {{ btnText }}<i class="el-icon-arrow-down el-icon--right"></i>
      </el-button>
      <el-dropdown-menu slot="dropdown">
        <template v-for="(col, index) in showColumns">
          <el-dropdown-item
            v-if="col.render"
            :key="index"
            :command="index"
          >
            <expandDom
              v-if="col.render"
              :key="index"
              :render="col.render"
            >
            </expandDom>
          </el-dropdown-item>
          <el-dropdown-item v-else :key="index" :command="index">{{
            col.text
          }}</el-dropdown-item>
        </template>
      </el-dropdown-menu>
    </el-dropdown>
    <template v-else>
      <template v-for="(col, index) in showColumns">
        <!-- render -->
        <expandDom
          v-if="col.render"
          :key="index"
          :row="row"
          :col="col"
          :render="col.render"
        ></expandDom>
        <el-button
          v-else
          :key="index"
          type="text"
          :size="size"
          @click="col.handler"
        >{{ col.text }}</el-button>
      </template>
    </template>
  </div>
</template>

<script>
export default {
  name: 'YButtonMore',
  components: {
    expandDom: {
      functional: true,
      props: {
        row: Object,
        col: Object,
        render: Function
      },
      render(h, ctx) {
        const params = {}
        if (ctx.props.col) {
          params.col = ctx.props.col
        }
        if (ctx.props.row) {
          params.row = ctx.props.row
        }
        return ctx.props.render && ctx.props.render(h, params)
      }
    }
  },
  props: {
    /*  */
    size: {
      type: String,
      default: 'mini' // medium, small, mini
    },
    /*  */
    max: {
      type: [String, Number],
      default: 3
    },
    /*  */
    row: {
      type: Object,
      default: () => {}
    },
    /*
      
      text: String 
      show: Function 
      handler: Function 
      render: Function render
     */
    columns: {
      type: Array,
      required: true
    },
    btnText: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      key: Math.random().toString(36).replace('.', ''),
      showColumns: []
    }
  },
  computed: {
    /*  */
    hasShowDropdown() {
      // 3
      return this.showColumns.length > this.max
    }
  },
  watch: {
    row: {
      handler(val) {
        this.showColumns = this.columns.filter((col) => {
          return col.show()
        })

        if (this.showColumns.length > this.max) {
          this.$nextTick(() => {
            this.setButtonStyle()
          })
        }
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    // 
    setButtonStyle() {
      const elButton = this.$refs.buttonMore?.querySelector('.el-button')
      if (elButton) {
        // primary
        elButton.classList.remove('el-button--primary')
        // plain
        elButton.classList.add('is-plain')
        // primary
        elButton.classList.add('color--primary')
      }
    },
    // 
    handleCommand(index) {
      this.showColumns[index].handler()
    }
  }
}
</script>

<style lang="scss" scoped>
.color--primary {
  color: $--color-primary;
  background: #ecf5ff;
  border-color: #b3d8ff;
}
</style>
